* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}

body {
    font-family: Arial, Arial, Helvetica, sans-serif;
    line-height: 1.4;
}

a {
    color: #333;
    text-decoration: none;
}

.container {
    padding: 0 1rem;
}

.btn {
    display: inline-block;
    border: none;
    background: #555;
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 15px;
}

.btnclear {
    display: inline-block;
    border: none;
    background: #555;
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 15px;
    margin: 8px;
}

.btn-hover {
    background: #666
}